export default  {
	data(){
		return{
			cartimage:[],
			hotgoods:[]
		}
	},
	onLoad(){
		this.getSwipers()
		this.getHotGoods()
},
methods:{
	//获取轮播图数据
	async getSwipers () {
		const res =await this.$myRequest({
			url :'/product/Swipers'
		})
		this.cartimage = res.data,
		console.log(this.cartimage)
	},
	//获取热门商品列表
	async getHotGoods(){
		const res =await  this.$myRequest({
			  url:'/product/Meat'
		  })
		  this.hotgoods = res.data,
		  console.log(this.hotgoods)
	 }
  },
}
